/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, main,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, main, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
body, .entry__content h4, .entry__content h5, .site-copyright, .site-title, .entry__content h3, .pagination, .section__hdr {
  font-weight: 400;
}

.entry__content h1, .entry__content h2, .entry__content h6, .entry__hd, .entry__content a, .page__all-updates a, .pagination a {
  font-weight: 700;
}

html,
body, .entry__content h4, .entry__content h5, .site-copyright {
  font-family: "Lato", sans-serif;
}

.site-title, .entry__content h3, .pagination, .section__hdr, .entry__content h1, .entry__content h2, .entry__content h6, .entry__hd {
  font-family: "Montserrat", sans-serif;
}

.entry__content a, .page__all-updates a, .pagination a {
  color: #d20000;
  font-family: inherit;
  text-decoration: none;
}

.entry__content a:hover, .page__all-updates a:hover, .pagination a:hover {
  border-bottom: 1px solid #d20000;
}

html,
body {
  font-size: 100%;
  line-height: 1.4;
}

html *,
body * {
  box-sizing: border-box;
}

img {
  height: auto;
  max-height: 90vh;
  max-width: 100%;
  width: auto;
}

