/*--------------------------------------------------------------------------------------------------------------------||
||  Header                                                                                                            ||
||--------------------------------------------------------------------------------------------------------------------*/
/*  TYPOGRAPHY: Header
------------------------------------------------------------*/
.site-description {
  font-size: 1rem;
}

@media (min-width: 640px) {
  .site-description {
    font-size: 1.125rem;
    letter-spacing: 0.02em;
  }
}

.site-details {
  line-height: 1.2;
}

.site-title {
  font-size: 1.0625rem;
  letter-spacing: 0.02em;
  line-height: 1;
  text-transform: uppercase;
}

@media (min-width: 640px) {
