  -webkit-background-size: 95px 79px;
  background-size: 95px 79px;
  display: inline-block;
  height: 79px;
  left: 0;
  padding: 0;
  position: relative;
  top: 0;
  width: 95px;
  margin: auto;
  z-index: 1;
}

