/*--------------------------------------------------------------------------------------------------------------------||
||  Navigation                                                                                                        ||
||--------------------------------------------------------------------------------------------------------------------*/
/*  TYPOGRAPHY: Navigation
------------------------------------------------------------*/
.jmw-main-menu a {
  text-decoration: none;
}

/*  DECORATION: Navigation
------------------------------------------------------------*/
.jmw-main-menu {
  background-color: #d2d2d2;
}

.jmw-main-menu a {
  color: black;
}

.jmw-main-menu a:hover {
  color: #d20000;
}

.menu-primary-navigation-container {
  background-color: #e1e1e1;
  box-shadow: rgba(0, 0, 0, 0.5) 1px 0 1px, rgba(0, 0, 0, 0.15) 1px 0 1px, rgba(0, 0, 0, 0.15) 1px 0 1px;
}

@media (min-width: 640px) {
  .menu-primary-navigation-container {
    box-shadow: rgba(0, 0, 0, 0.15) 1px 0 1px, rgba(0, 0, 0, 0.15) 1px 0 1px;
  }
}

.menu-primary-navigation-container:before, .menu-primary-navigation-container:after {
  background-color: black;
}

/*  LAYOUT: Navigation
------------------------------------------------------------*/
/* Menu container elements */
.jmw-main-menu {
  padding: 2rem;
  position: relative;
  transition: top 125ms ease-out;
  top: -300px;
}

@media (min-width: 640px) {
  .jmw-main-menu {
    height: 100vh;
    left: -300px;
    margin: 0 2rem 0 0;
    padding: 2rem 0 0 2rem;
    top: auto;
    transition: left 125ms ease-out;
  }
}

@media (min-width: 640px) {
  .logged-in .jmw-main-menu {
    padding-top: calc(2rem + 32px);
  }
}

.jmw-main-menu .menu-item + .menu-item {
  margin-top: 0.25em;
}

.jmw-main-menu .menu-item > .sub-menu {
  margin-left: 0.75em;
  padding-top: 0.25em;
}

body:not(.touch-device) .menu-primary-navigation-container:hover .jmw-main-menu,
.menu-primary-navigation-container.js--nav-open .jmw-main-menu {
  top: 0;
  transition: top 125ms ease-in-out;
}

@media (min-width: 640px) {
  body:not(.touch-device) .menu-primary-navigation-container:hover .jmw-main-menu,
  .menu-primary-navigation-container.js--nav-open .jmw-main-menu {
    left: 0;
    top: auto;
    transition: left 125ms ease-in-out;
  }
}

.menu-primary-navigation-container {
  height: 30px;
  position: fixed;
  transition: height 125ms ease-out;
  z-index: 2;
  width: 100%;
  /* Menu container indicator */
}

@media (min-width: 640px) {
  .menu-primary-navigation-container {
    bottom: 0;
    height: 100vh;
    top: 0;
    width: 30px;
  }
}

.menu-primary-navigation-container:before, .menu-primary-navigation-container:after {
  content: '';
  display: block;
  height: 3px;
  margin: auto;
  position: absolute;
  top: 45%;
  width: 10px;
}

@media (min-width: 640px) {
  .menu-primary-navigation-container:before, .menu-primary-navigation-container:after {
    left: auto;
    right: 10px;
    top: auto;
    transition: transform 325ms ease-in-out, top 325ms ease-in-out;
  }
}

@media (min-width: 0px) and (max-width: 639px) {
  .menu-primary-navigation-container:before {
    left: -5px;
    right: 0;
    transform: rotate(45deg);
  }
}

@media (min-width: 640px) {
  .menu-primary-navigation-container:before {
    top: 90px;
    transform: rotate(-45deg);
  }
}

@media (min-width: 0px) and (max-width: 639px) {
  .menu-primary-navigation-container:after {
    left: 0;
    right: -5px;
    transform: rotate(135deg);
  }
}

@media (min-width: 640px) {
  .menu-primary-navigation-container:after {
    top: 95px;
    transform: rotate(-135deg);
  }
}

@media (min-width: 640px) {
  .logged-in .menu-primary-navigation-container:before {
    top: 122px;
  }
}

@media (min-width: 640px) {
  .logged-in .menu-primary-navigation-container:after {
    top: 127px;
  }
}

/* Menu container interactions */
.menu-primary-navigation-container.js--nav-open,
body:not(.touch-device) .menu-primary-navigation-container:hover {
  height: auto;
  padding-bottom: 30px;
  transition: height 125ms ease-in-out;
  /* Menu container indicator */
}

@media (min-width: 640px) {
  .menu-primary-navigation-container.js--nav-open,
  body:not(.touch-device) .menu-primary-navigation-container:hover {
    width: 300px;
    transition: width 125ms ease-in-out;
  }
}

.menu-primary-navigation-container.js--nav-open:before, .menu-primary-navigation-container.js--nav-open:after,
body:not(.touch-device) .menu-primary-navigation-container:hover:before,
body:not(.touch-device) .menu-primary-navigation-container:hover:after {
  bottom: 12px;
  top: auto;
}

@media (min-width: 640px) {
  .menu-primary-navigation-container.js--nav-open:before, .menu-primary-navigation-container.js--nav-open:after,
  body:not(.touch-device) .menu-primary-navigation-container:hover:before,
  body:not(.touch-device) .menu-primary-navigation-container:hover:after {
    bottom: auto;
    right: 10px;
    transition: transform 325ms ease-in-out, top 325ms ease-in-out;
  }
}

.menu-primary-navigation-container.js--nav-open:before,
body:not(.touch-device) .menu-primary-navigation-container:hover:before {
  transform: rotate(-45deg);
}

@media (min-width: 640px) {
  .menu-primary-navigation-container.js--nav-open:before,
  body:not(.touch-device) .menu-primary-navigation-container:hover:before {
    top: 95px;
    transform: rotate(135deg);
  }
}

.menu-primary-navigation-container.js--nav-open:after,
body:not(.touch-device) .menu-primary-navigation-container:hover:after {
  transform: rotate(-135deg);
}

@media (min-width: 640px) {
  .menu-primary-navigation-container.js--nav-open:after,
  body:not(.touch-device) .menu-primary-navigation-container:hover:after {
    top: 90px;
    transform: rotate(45deg);
  }
}

